.listbox {
    display: flex;
    height: 180px;
    border: 1px solid rgb(225, 225, 225);
    justify-content: left;
    background-color: rgb(255, 255, 255);
    align-items: center;
}

.listdesc{
    text-align: left;
    margin-left: 30px;
    font-weight: 600;
}

.ImagePart {
    /* display: flex; */
    width: 240px;
    /* align-items: center; */
    margin-left: 20px;
    /* margin-top: 20px;
    margin-bottom: 20px; */
}

.img{
    width: 240px;
    height: 150px;
    object-fit: cover;
}

.user_name {
    font-size: 15px;
    font-weight: 600;
    color: black;
    margin-bottom: 15px;
}

.store_name {
    font-size: 20px;
    font-weight: 600;
    color: black;
    margin-bottom: 7px;
}

.review_title{
    font-size: 15px;
    font-weight: 600;
    color: black;
}

.review_desc{
    font-size: 10px;
    font-weight: 600;
    color: rgb(70, 70, 70);
}

